@import url('https://fonts.googleapis.com/css2?family=Edu+SA+Beginner:wght@600&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Edu+SA+Beginner:wght@600&family=Rubik&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Inter&family=Rubik&display=swap');

:root {
  --optionheight: 85vh;
  --questionheight: 15vh;
  --optionfontsize: 2.5em;
  --optionhoverfontsize: 2.5em;
  --questionfontsize: 1.8em;
}

body {
  /* font-family: 'Edu SA Beginner', cursive; */
  font-family: 'Inter', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.main-container {
  height: 100%;
  width: 100vw;
  position: fixed;
  top: 0px;
  overflow: hidden
}

.options-container {
  position: relative;
}

.left-side-container {
  background-color: #000000;
  width: 50vw;
  height: 85vh;
  bottom: 0px;
  position: fixed;
  left: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.left-side-container:hover, .right-side-container:hover {
  cursor: pointer;
}

.right-side-container {
  background-color: #FFFFFF;
  width: 50vw;
  /* height: var(--optionheight); */
  height: 85vh;
  bottom: 0px;
  position: fixed;
  right: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.right-side-container:hover > .right-option {
  cursor: pointer;
  /* animation: levitating 1s ease-in-out infinite; */
}

.left-side-container:hover > .left-option {
  cursor: pointer;
  /* animation: levitating 1s ease-in-out infinite; */
}

.left-option, .left-option-nh {
  font-size: var(--optionfontsize);
  color: white;
  width: 27vw;
}

.left-option-container-nh, .right-option-container-nh {
  width: 27vw;
}

.stat {
  margin-top: 30px;
  font-size: 2em;
  color: white;
}

.stat2 {
  margin-top: 30px;
  color: black;
  font-size: 2em;
}

.right-option, .right-option-nh {
  font-size: var(--optionfontsize);
  width: 27vw;
}

.right-option-nh, .left-option-nh {
  margin-top: 100px;
}


.question-container {
  background-color: #708090;
  /* height: var(--questionheight); */
  height: 15vh;
  /* height: clamp(10vh, 15vh, 30vh); */
  /* min-height: 15vh;
  max-height: 50vh; */
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  /* background-color: red; */
  /* padding-bottom: 3em; */
}



.add-btn:hover {
  cursor: pointer;
}

.question {
  /* font-size: var(--questionfontsize); */
  /* font-size: clamp(1.2em, 1.8em + 1vw, 2.4em); */
  font-size: clamp(1em, 1.6em, 1.8em);
  width: 85vw;
  margin-left: 3vw;
  color: white;
}

@keyframes levitating {
  0% {
    transform: translate(0px,0px);
  }
  50% {
    transform: translate(0px, 15px);
    font-size: var(--optionhoverfontsize);
  }
  100% {
    transform: translate(0px, -0px);
  }
}

.left-option:hover, .right-option:hover {
  cursor: pointer;
  /* animation: levitating 1s ease-in-out infinite; */
}

/* Modal Stuff Below */

.modal-container {
  animation-name: fadeIn;
  animation-duration: 0.5s;
  animation-iteration-count: 1;
  font-size: 17px;
  position: fixed;
  background-color: #f9c667;
  z-index: 9999;
  top: 50%;
  left: 50%;
  width: 300px;
  height: 470px;
  border-radius: 5px;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 10px rgba(0,0,0,0.2);
  padding: 10px;
}

.modal-close-btn:hover {
  cursor: pointer;
}

.modal-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.modal-title {
  margin-top: 5px;
  margin-left:100px;
  font-size: 23px;
  color: #800020;
}

.modal-form {
  height: 250px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}

#name {
  padding: 2px;
  font-family: 'Inter', sans-serif;
  font-size: 17px;
  width: 290px;
  height: 30px;
}

.idea-lbl {
  margin-top: 10px;
}
#idea {
  padding: 2px;
  font-family: 'Inter', sans-serif;
  font-size: 17px;
  width: 290px;
  height: 150px;
  resize: none;
}

.submit-btn-container {
  width: 290px;
  margin-top: 25px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.submit-btn {
  background-color: #FF6F61;
  color: white;
  font-weight: bold;
  font-size: 20px;
  padding: 10px;
  border-radius: 5px;
  border: none;
  /* border: 1px white solid; */
}

.error-msg {
  color: rgb(196, 0, 0);
  margin-bottom: 2px;
  font-weight: bold;
}

.success-msg {
  color: green;
  margin-bottom: 2px;
  font-weight: bold;
}

.submit-btn:hover {
  cursor: pointer;
}

.overlay {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
}

@keyframes fadeIn {
  0% {opacity: 0;}
  100% {opacity: 1;}
}

.more-info-btn {
  display: none;
}

